.label {
  display: inline-block;min-width: 10px;padding: 3px 7px;font-size: 12px;font-weight: 400;line-height: 0.9em;color: #fff;text-align: center;white-space: nowrap;vertical-align: middle;background-color: #777777;border-radius: 0px;
}
.label {
  font-size: 10.5px;
  line-height: 0.9em;
  padding: 2px 8px;
}
.label--fixed-width {
  min-width: 80px;
  text-align: center;
}
.label--primary {
  background-color: #de3473;
}
.label--black {
  background-color: #303030;
}
.label--grey {
  background-color: #bebebe;
}
.label--grey-dark {
  background-color: #575757;
}
.label--blue {
  background-color: #3ebbeb;
}
.label--blue-outline {
  background-color: transparent;
  border: 1px solid #3ebbeb;
  color: #3ebbeb;
}
.label--orange {
  background-color: #ff9000;
}
.label--yellow {
  background-color: #ffba00;
}
.label--green {
  background-color: #00a651;
}
.label--red {
  background-color: #f4044c;
}
.label:empty {
  padding: 0;
  border: none;
}
